.root {
  @apply flex flex-col py-10;
  min-height: 100vh;
  @media screen and (max-height: 500px) {
    @apply py-2;
  }
  .mainBlock {
    @apply inline-flex flex-col px-4 mx-auto items-center mb-5;
    @media screen and (max-height: 500px) {
      @apply mb-1;
    }
    .logoBlock {
      @apply flex justify-center relative mb-5 w-full;
      .img {
        width: 70px;
        height: auto;
      }
      .icon {
        @apply absolute;
        top: 5px;
        width: 26px;
        &.left {
          left: 5px;
          transform: rotate(-15deg);
        }
        &.right {
          right: 5px;
          transform: rotate(15deg);
        }
      }
    }
    .title {
      @apply m-0 mb-5 text-center text-2xl font-bold;
      @media screen and (max-height: 500px) {
        @apply text-lg mb-1;
      }
    }
    .description {
      @apply font-bold m-0;
      letter-spacing: 0.7px;
      color: #698a9f;
      @media screen and (max-height: 500px) {
        @apply text-sm;
      }
    }
    .content {
      @apply flex flex-col justify-center items-center;
    }

    @media screen and (max-height: 500px) {
      .logoBlock {
        width: 200px;
        @apply mb-0;
      }
      @apply py-2 flex-row w-auto;
    }
  }
  .donate {
    @apply m-auto;

    .card {
      @apply py-6 inline-flex flex-col;

      background-color: #fff;
      box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.1);
      border-radius: 20px;
      .title {
        @apply px-6 text-center mb-2;
      }
      @media screen and (max-height: 500px) {
        @apply py-2;
        .title {
          @apply mb-1 text-lg;
        }
      }
      .content {
        @apply flex flex-col;
        .options {
          @apply flex flex-wrap px-3 justify-around;
          .option {
            @apply p-3;
          }
        }
        .other {
          @apply px-6 mt-2;
          .button {
            @apply w-full;
          }
        }
        @media screen and (max-height: 500px) {
          @apply flex-row;
          @apply px-3;
          .other {
            @apply m-0 p-1;
          }
          .options {
            @apply px-0;
            .option {
              @apply p-1;
            }
          }
        }
      }
    }
  }
  .amountBlock {
    @apply flex flex-col h-full items-start;

    .inputWrapper {
      @apply flex items-center relative justify-center my-auto;
      width: calc(50% + 60px);
      .input {
        @apply p-0 h-16 pr-10 text-right text-4xl w-full font-bold;
        letter-spacing: 1px;
        border: none;
        outline: none;
        color: var(--secondary-text-color);
        -moz-appearance: textfield;

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0;
        }
        &:focus {
          & + .currency {
            color: var(--secondary-text-color);
          }
        }
      }
      .currency {
        @apply text-5xl absolute top-0 bottom-0 right-0 my-auto flex items-center pointer-events-none w-8 transition-colors;
        color: #757575;
        &.active {
          color: var(--secondary-text-color);
        }
      }
    }
    .amountBlockFooter {
      @apply p-3 pb-10 flex justify-center w-full;
      .button {
        max-width: 230px;
        width: 100%;
      }
      @media screen and (max-height: 500px) {
        @apply p-2;
      }
    }
  }
  .submit {
    @apply mx-auto mt-4;
    .button {
      width: 230px;
    }
    @media screen and (max-height: 500px) {
      @apply mt-2;
    }
  }
}
