.root {
  @apply inline-flex items-center relative cursor-pointer py-3 px-5 justify-center transition-colors;
  border: 2px solid #30336b;
  user-select: none;
  background-color: #ffffff;

  &.secondary {
    background-color: #1376f7;
    border-color: #1376f7;
    color: #fff;
  }
  &.disabled {
    @apply opacity-60;
  }
  &.check {
    @media screen and (max-height: 500px) {
      @apply py-1;
    }
  }
  .icon {
    @apply absolute pointer-events-none;
    transform: rotate(5deg);
    width: 24px;
    top: -6px;
    right: -6px;
  }
}
