.root {
  @apply fixed overflow-hidden w-full left-0 top-0 bottom-0 flex flex-col;
  background-color: #ffffff;

  .header {
    @apply px-10 py-2 flex justify-center items-center;
    height: 50px;
    .title {
      @apply text-xl font-bold;
    }
    .close {
      @apply absolute w-12 h-12 top-0 right-0 flex items-center cursor-pointer p-3;

      &:after,
      &:before {
        @apply absolute block w-6;
        content: "";
        height: 2px;
        background-color: red;
      }
      &:after {
        transform: rotate(45deg);
      }
      &:before {
        transform: rotate(-45deg);
      }
    }
  }

  .wrapper {
    @apply h-full flex flex-col overflow-auto;
  }
}
